import React, { useState } from 'react'
import Swapi from './Swapi'

function Main() {

  const [number, setNumber] = useState('1')

  return (
    <div>
      <input type='number' defaultValue='1' onChange={(e) => {
        setNumber(e.target.value)
      }}></input>
      <Swapi number={number} />
    </div>
  )
}

export default Main
