import React, { useState, useEffect } from 'react'
import axios from 'axios'
import '../App.css'

function Starships({ hero }) {
  console.log(hero.starships)

  const [starships, setStarships] = useState([])

  useEffect(() => {

    if (hero.starships) {
      hero.starships.forEach(async (element) => {
        let starhipsqwe = await axios.get(element).then((r) => r.data)
        setStarships((a) => [...a, starhipsqwe.name])
      });
    }
    return () => {
      setStarships('')
    }
  }, [hero])

  return (
    <div className="starships">
      Корабли:
      <ul>
        {
          Object.values(starships).map((el) => <li>{el}</li>)
        }
      </ul>
    </div>
  )
}

export default Starships
