import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Starships from './Starships'
import '../App.css'


function Swapi({ number }) {

  const [hero, setHero] = useState('')

  useEffect(() => {
    const getHero = async () => {
      let response = await axios.get(`https://swapi.dev/api/people/${number}/`).then((r) => r.data)
      setHero(response)
      return hero
    }
    getHero()
  }, [number])

  return (
    <div className='swapi'>
      Имя : {hero.name}
      <Starships hero={hero} />
    </div>
  )
}

export default Swapi
